.GameWrapper {
  color: #ddd;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  vertical-align: middle;
  flex-direction: column;
}

.TopBar {
  height: 20vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  font-size: 36px;
  .BoardPart {
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    width: 80%;
  }
  .Title {
    font-family: "Kode Mono", monospace;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-size: 182px;
    font-weight: 700;
    line-height: 232.96px;
    letter-spacing: -0.08em;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    position: relative;
  }
  .HighscorePart {
    display: flex;
    justify-content: start;
    align-items: center;
    vertical-align: middle;
    width: 20%;
    font-weight: bold;
    max-height: 80%;
    overflow: scroll;
  }
}
.CurrentLevel {
  text-align: center;
  width: 50%;
}
.CurrentScore {
  text-align: center;
  width: 50%;
}
.Content {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.BoardWrapper {
  width: 80%;
}
.SideBar {
  height: 80vh;
  width: 20%;
  color: #dddddd;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Countdown {
  color: red;
  font-size: 48px;
}

.NewGameButtonWrapper {
  display: flex;
  flex-direction: column;
  justify-content: baseline;
}

.NewGameButton {
  width: 7.5rem;
  height: 7.5rem;
}

.CreateWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.25);
  .CreateModal {
    display: flex;
    flex-direction: column;
    justify-content: start;
    color: black;
    background-color: #ccc;
    width: 60vw;
  }
  .Button {
    width: 100%;
    text-align: center;
  }
}
